* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  padding: 10px;

}


.country-element {
  display: flex;
  align-items: center;
  
}

.country-name{
  margin-left: 30px;
}

.country-info {
  padding-left: 20px;
}

.country-title{
  display: flex;
  align-items: center;
}

.country-flag {
  background-size: cover;
}

.title-text {
  margin-left: 10px;
  font-size: 32px;
}

.country-description {
  font-size: 16px;
  font-weight: bold;
}

.country-description-text {
  font-size: 15px;
  font-weight: normal;
}

